<template>
    <div id="mychart"></div>
</template>

<script>
import * as echarts from 'echarts'
export default {
    data() {
        return {

        }
    },
    mounted() {
        this.initChart()
    },
    methods: {
        initChart() {
            var myChart = echarts.init(document.getElementById('mychart'));
            const imglist = {
                "晴": 'data:image/png;base64,',
                "小雨": "data:image/png;base64,",
                "阴": "data:image/png;base64,",
                "多云": "data:image/png;base64,"
            };
            const img = 'data:image/png;base64,';
            const maxTemp = ["16.3", "16.2", "20.6", "14.2", "17.6", "15.7", "14.3"];
            const minTemp = ["-5.4", "-2.8", "-3.5", "-2.5", "12.4", "-1.2", "-13"];

            const weekday = ["周一", "周二", "周三", "周四", "周五", "周六", "周日"];
            const daylist = ["25日", "26日", "27日", "28日", "29日", "30日", "31日"];
            const weatherlist = ["小雨", "小雨", "阴", "小雨", "多云", "小雨", "小雨"];
            //数据转换成rich对象
            let style = weatherlist.map((i) => {
                return {
                    backgroundColor: {
                        image: imglist[i]
                    },
                    height: 40,
                    width: 40
                }
            });

            let rich = {}
            style.forEach((i, index) => {
                rich[index] = i;
            });
            rich.b = {
                // color: 'white',
                fontSize: 12,
                lineHeight: 30,
                height: 20
            };

            myChart.setOption({
                backgroundColor: "#fff",
                title: {
                    text: "7天天气预报",
                    left: "5%",
                    show: false
                },
                tooltip: {
                    trigger: "axis",
                    axisPointer: {
                        type: "shadow",
                        shadowStyle: {
                            color: "rgba(99,166,253,0.1)"
                        },
                        shadowBlur: 10
                    },
                    formatter: function (params) {
                        let str = weekday[params[0].dataIndex] + "," + params[0].name + "<br />";
                        params.forEach((item, index) => {
                            console.log(item);
                            str += `${item.marker}${item.seriesName}: ${item.data}<br />`;
                            if (index == params.length - 1) {
                                str += `${item.marker}天气：${weatherlist[item.dataIndex]}<br />`;
                            }

                        });
                        return str;
                    }

                },
                legend: {
                    show: false
                },
                grid: {
                    show: true,
                    backgroundColor: "transparent",
                    opacity: 0.3,
                    borderWidth: "0",
                    top: "20%",
                    bottom: "20%"
                },
                toolbox: {
                    show: false,
                    right: "5%",
                    feature: {
                        magicType: {
                            type: ["line", "bar", "stack"]
                        },
                        dataZoom: {
                            yAxisIndex: "none"
                        },
                        restore: {},
                        saveAsImage: {}
                    }
                },
                xAxis: [
                    // 日期
                    {
                        type: "category",
                        boundaryGap: false,
                        position: "top",
                        offset: -50,
                        zlevel: 100,
                        axisLine: {
                            show: false
                        },
                        axisTick: {
                            show: false
                        },
                        axisLabel: {
                            interval: 0,
                            formatter: ["{a|{value}}"].join("\n"),
                            rich: {
                                a: {
                                    // color: 'white',
                                    fontSize: 18
                                }
                            }
                        },
                        nameTextStyle: {},
                        data: daylist
                    },
                    // 星期
                    {
                        type: "category",
                        position: "bottom",
                        offset: -50,
                        boundaryGap: false,
                        zlevel: 100,
                        axisLine: {
                            show: false
                        },
                        axisTick: {
                            show: false
                        },
                        axisLabel: {
                            interval: 0,
                            formatter: ["{a|{value}}"].join("\n"),
                            rich: {
                                a: {
                                    // color: 'white',
                                    fontSize: 14
                                }
                            }
                        },
                        nameTextStyle: {
                            fontWeight: "bold",
                            fontSize: 19
                        },
                        data: weekday
                    },
                    // 天气图标
                    {
                        type: "category",
                        boundaryGap: false,
                        position: "top",
                        offset: -120,
                        zlevel: 100,
                        axisLine: {
                            show: false
                        },
                        axisTick: {
                            show: false
                        },
                        axisLabel: {
                            interval: 0,
                            formatter: function (value, index) {
                                return "{k|}\n{" + index + "| }\n{b|" + value + "}";
                            },
                            rich
                        },
                        nameTextStyle: {
                            fontWeight: "bold",
                            fontSize: 19
                        },
                        data: weatherlist
                    }
                ],
                yAxis: {
                    show: false,
                    type: "value",

                    axisLabel: {
                        formatter: "{value}" + "℃"
                    },
                    max: function (value) {
                        return value.max + 20;
                    },
                    min: function (value) {
                        return value.min - 20;
                    }
                },
                series: [
                    {
                        name: "最高温度",
                        type: "line",
                        data: maxTemp,
                        symbol: "emptyCircle",
                        symbolSize: 10,
                        showSymbol: true,
                        smooth: true,
                    },
                    {
                        name: "最低温度",
                        type: "line",
                        data: minTemp,
                        symbol: "emptyCircle",
                        symbolSize: 10,
                        showSymbol: true,
                        smooth: true,
                    }
                ]
            }
        )
        }
    }
}
</script>

<style scoped>
#mychart {
    width: 500px;
    height: 500px;
}
</style>