import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
    {
        path: '/home',
        name: 'Home',
        component: () => import("../views/Home.vue")
    },
    {
        path: '/test',
        name: 'Test',
        component: () => import("../views/Test.vue")
    },
    {
        path: '/testyuyin',
        name: 'Testyuyin',
        component: () => import("../views/testyuyin.vue")
    },
    {
        path: '/testSubscribe',
        name: "TestSubscribe",
        component: () => import("../views/TestSubscribe.vue")
    },
    {
        path: '/mapPage',
        name: 'MapPage',
        component: () => import("../views/MapPage.vue")
    },
    {
        path: '/testTree',
        name: 'TestTree',
        component: () => import("../views/testTree.vue")
    }
]


const router = new VueRouter({
    mode: "history",
    routes
})


export default router
